<template>
  <div class="wrapper">
    <LanguageButton
      :languages="languages"
      @select-language="onSelectLanguage"
    />
    <div id="main" class="section page-header header-filter first-section">
      <div
        class="image"
        :style="{
          backgroundImage: `url(${this.header})`
        }"
      ></div>
      <div class="container">
        <div class="md-layout">
          <div
            class="md-layout-item md-size-50 md-small-size-70 md-xsmall-size-100"
          >
            <h1 class="title white-back caveat">
              {{
                selectedLanguageIndex === 0
                  ? "Меня зовут Янушевская-Олтушец Вероника Александровна и я режиссёр мероприятий."
                  : "My name is Yanushevskaya-Oltushets Veronika Aleksandrovna and I am an event director."
              }}
            </h1>
            <h4 class="white-back">
              {{
                selectedLanguageIndex === 0
                  ? "«Знаю тонкости работы на площадках различного масштаба, реализую проекты в кратчайшие сроки в дружелюбной и профессиональной атмосфере. Результат работы небезразличен и неизменно высок»"
                  : "«I know the intricacies of working on sites of various sizes, I implement projects in the shortest possible time in a friendly and professional atmosphere. The result of the work is not indifferent and is consistently high.»"
              }}
            </h4>
            <br />
            <div class="black-divider"></div>
            <md-button
              href="https://www.youtube.com"
              class="md-success md-lg"
              target="_blank"
              ><i class="fas fa-play"></i>
              {{
                selectedLanguageIndex === 0 ? "Посмотреть видео" : "Watch video"
              }}
            </md-button>
          </div>
        </div>
      </div>
    </div>
    <Profile> </Profile>
    <Diplomas> </Diplomas>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

import LanguageButton from "./components/LanguageButton.vue";
import Profile from "../views/Profile.vue";
import Diplomas from "../views/Diplomas.vue";

export default {
  components: {
    Diplomas,
    LanguageButton,
    Profile
  },

  bodyClass: "landing-page",
  props: {
    header: {
      type: String,
      default: require("@/assets/img/photo_1.png")
    },
    teamImg1: {
      type: String,
      default: require("@/assets/img/faces/avatar.jpg")
    },
    teamImg2: {
      type: String,
      default: require("@/assets/img/faces/christian.jpg")
    },
    teamImg3: {
      type: String,
      default: require("@/assets/img/faces/kendall.jpg")
    }
  },
  data() {
    return {
      name: null,
      email: null,
      languages: [
        { name: "Ru", isSelected: true },
        { name: "En", isSelected: false }
      ],
      message: null
    };
  },
  computed: {
    ...mapState(["selectedLanguageIndex"]),
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`
      };
    },
    selectedLanguage() {
      return this.languages.find(language => language.isSelected).name;
    }
  },
  methods: {
    ...mapMutations([
      // mapMutations также поддерживает нагрузку:
      "setSelectedLanguageIndex" // `this.setHomePageData(amount)` будет вызывать `this.$store.commit('setHomePageData', amount)`
    ]),
    onSelectLanguage(selectedName) {
      this.languages.map(language =>
        language.name === selectedName
          ? (language.isSelected = true)
          : (language.isSelected = false)
      );
      this.setSelectedLanguageIndex(this.selectedLanguage === "Ru" ? 0 : 1);
    }
  }
};
</script>

