<template>
  <div id="profile" class="main main-raised">
    <div class="section profile-content">
      <div class="container">
        <div class="profile-tabs">
          <tabs
            :tab-name="tabName[selectedLanguageIndex]"
            plain
            nav-pills-icons
            color-button="success"
          >
            <!-- here you can add your content for tab-content -->
            <template slot="tab-pane-1">
              <div class="md-layout">
                <!--                <div class="md-layout-item md-size-25 ml-auto">-->
                <div class="md-layout-item column-wrapper ml-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane1.slice(
                      0,
                      tabPane1.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="режиссура"
                  />
                </div>
                <div class="md-layout-item column-wrapper mr-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane1.slice(
                      tabPane1.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="directing"
                  />
                </div>
              </div>
            </template>
            <template slot="tab-pane-2">
              <div class="md-layout">
                <div class="md-layout-item column-wrapper ml-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane2.slice(
                      0,
                      tabPane2.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="мероприятия"
                  />
                </div>
                <div class="md-layout-item column-wrapper mr-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane2.slice(
                      tabPane2.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="events"
                  />
                </div>
              </div>
            </template>
            <template slot="tab-pane-3">
              <div class="md-layout">
                <div class="md-layout-item column-wrapper ml-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane3.slice(
                      0,
                      tabPane3.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="сценарист"
                  />
                </div>
                <div class="md-layout-item column-wrapper mr-auto">
                  <img
                    v-for="(tabPanel, index) in tabPane3.slice(
                      tabPane3.length / 2
                    )"
                    :key="index"
                    :src="tabPanel.src.url"
                    class="rounded"
                    alt="screenwriter"
                  />
                </div>
              </div>
            </template>
          </tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { Tabs } from "@/components";
import { request } from "@/datocms";

const PHOTO_QUERY = `query MyQuery {
  allPhotos(first: 100) {
    description
    src {
      url
    }
    group
  }
}`;

export default {
  components: {
    Tabs
  },
  bodyClass: "profile-page",
  props: {
    header: {
      type: String,
      default: require("@/assets/img/city-profile.jpg")
    },
    img: {
      type: String,
      default: require("@/assets/img/faces/christian.jpg")
    }
  },
  data() {
    return {
      photos: [],
      data: null,
      loading: true,
      tabName: [
        ["Студия", "Работа", "Жизнь"],
        ["Studio", "Work", "Live"]
      ]
    };
  },
  computed: {
    ...mapState(["selectedLanguageIndex"]),
    tabPane1() {
      return this.photos.filter(
        photo => photo.group.toLowerCase() === "studio"
      );
    },
    tabPane2() {
      return this.photos.filter(photo => photo.group.toLowerCase() === "work");
    },
    tabPane3() {
      return this.photos.filter(photo => photo.group.toLowerCase() === "live");
    },
    headerStyle() {
      return {
        backgroundImage: `url(${this.header})`
      };
    }
  },
  async mounted() {
    try {
      this.data = await request({
        query: PHOTO_QUERY
      });
    } catch (e) {
      this.error = e;
    } finally {
      const responseData =
        this.data && this.data.allPhotos && this.data.allPhotos[0];
      if (responseData) {
        this.photos = this.data.allPhotos;
      }
    }
    this.loading = false;
  }
};
</script>

